@import '../../modules';

.custom{

  section{
    text-align: center;
    h2, h3{
      @include style_font(700, 3.6rem, 1, #1f1f1f);
      padding-bottom: 7.4rem;
    }
  }

  .pagination{
    margin-top: 4.8rem;
  }

  .img-no-result{
    position: absolute;
    bottom: 0;
    left: -7.1rem;
    display: block;

    width: 20.4rem; 
    height: 29.8rem;
  }

  .btn-no-result{
    width: 10.9rem;
    height: 3.4rem;
    padding: 0;
    margin-left: 1.7rem;
    margin-right: 1.9rem;
    font-size: 1.4rem;
    column-gap: 4px;

    // 연필 아이콘
    i.icon{
      width: 1.2rem;
      height: 1.2rem;
      background-image: none;
      margin-left: 0;
    }
  }
}

@include mobile-max{
  .custom{

    section{
      h2, h3{
        padding-bottom: 4rem;
        font-size: 1.6rem;
      }
    }

    .pagination{
      margin-top: 3rem;
    }
  }
}