@import '../../modules';

.div-osp7117{
  padding-top: 7.9rem;
  padding-bottom: 10.2rem;
  text-align: center;

  .board-list{
    margin-bottom: 0;
  }
  
  .board-detail{
    min-height: 70.2rem;
    background-color: $bg_gray_f;
    padding: 4.4rem 4.8rem;
    border-radius: 0 0 2rem 2rem;
    
    p{
      text-align: left;
      @include style_font(400, 2rem, 2em, #4b4b4b);
    }
  }

  .btn-link-list{
    margin-top: 4.4rem;
  }

}

@include mobile-max{
  .div-osp7117{
    padding-bottom: 4rem;
    
    .board-detail{
      min-height: 24.2rem;
      padding: 1.6rem 2.2rem;
      p{
        font-size: 1.4rem;
      }
    }
  }
}