@import '../../modules';


.wrap-content.promote-boardsView {
  text-align: center;
  padding-bottom: 20.4rem;
  background-color: #F8F9FA;
  padding: 96px 0;
  overflow: hidden;

  .view {

    .content {
      .block {
        display: block;
      }

      .blue {
        color: #3855FF;
        font-weight: bold;
      }
    }

    .logo-wrap {
      width: 100%;
      height: 12rem;
      display: flex;
      justify-content: center;
      align-items: center;

      margin-bottom: 4.8rem;

      .logo {
        display: inline-block;
        width: 17.2rem;
        height: 4rem;
        background-image: url('../../../../public/asset/icon/logo_blue.svg');
        background-size: cover;
      }
    }
  }

  .btn__wrapper {
    position: absolute;
    margin-top: 2.4rem;
    width: 100%;
    text-align: center;
  }

  a.btn-link-list {
    margin-top: 0;
    background-color: white;
  }
}

// * 👽️responsive
@include mobile-max {
  .wrap-content.promote-boardsView {
    padding: 4rem 2rem 10.6rem 2rem;

    .view {
      .content {
        padding-top: 2.4rem;

        .blue {
          font-weight: 400;

        }

      }
    }

    .btn__wrapper {
      width: calc(100% - 4rem);
    }
  }
}