@import '../../modules';


.board__wrapper.qna{

  .board-list{
    
    .qna-question{
      display: grid;
      grid-template-columns: minmax(20rem, 12%) 86%;
      align-items: center;
      column-gap: 8px;
      height: 9.6rem;

      background-color: $white;
      border-bottom: 1px solid #ececec;

      .--col1{
        padding-left: 2.6rem;
        width: max-content;
        justify-items: self-start;
      }
      
      .btn-board-toggle{
        display: inline-flex;
        justify-content: space-between;
        align-items: center;
      }
    }

    .qna-answer{
      display: block;

      .qna-text__wrapper{
        // grid-area: cnt;
        text-align: left;
        padding: 4.4rem 4.8rem 3.2rem 4.8rem;
        background-color: $bg_gray_f;
        border-radius: 0 0 3rem 3rem;

        p{
          margin-bottom: 6.4rem;
          @include style_font(400, 2rem, 2em, #4b4b4b);
        }

      }

      .no-res{
        span{
          @include style_font(400, 1.4rem, 1, $azure);
        }
        width: max-content;
        margin-left: auto;
        
        .btn{
          width: 10.9rem; 
          height: 3.4rem;
          font-size: 14px;
          margin-left: 1rem;
  
          .icon{
            width: 1.4rem;
            height: 1.6rem;
            margin-left: 5px;
          }
        }
      }

      visibility: hidden;
      height: 0;
      transform: scaleY(0.5);
      transform-origin: top;
      transition: transform 0.2s ease-out;
    }

    .qna-answer.--open{
      visibility: visible;
      height: auto;
      transform: scaleY(1);
    }
  }
}

@include mobile-max{
  .board__wrapper.qna{

    .board-list{
      padding: 0;

      .qna-question{
        padding: 1.3rem 2rem;
        grid-template-columns: none;
        grid-template-areas:
          "cate date"
          "cont cont";
        
        .--col1{
          padding-left: 0;
          grid-area: cate;
        }
        
        .btn-board-toggle{
          width: 100%;
          // height: max-content;
          grid-area: cont;
          padding: 1.5rem 0 1rem 0;
          margin-left: 2px;
          font-size: 1.4rem;
  
          span{
            width: calc(100% - 1.6rem);
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            text-align: left;
            font-weight: 700;
            color: $font_color-212121_grey;
          }
  
          .icon{
            width: 1.6rem;
            height: 1.6rem;
          }
        }
      }

      .qna-answer{
        padding: 0;

        .qna-text__wrapper{
          padding: 1.6rem 2.2rem;
          border-radius: 0 0 1.5rem 1.5rem;
          
          p{
            margin-bottom: 2.4rem;
            font-size: 1.4rem;
            font-weight: 700;
            color: $font_color-212121_grey;
          }

          .no-res{
            span{
              font-size: 1rem;
              font-weight: 700;

            }

            .btn{
              width: 8rem;
              height: 2.4rem;
              font-size: 1rem;
            }
          }
        }
      }
    }
  }
}