@import '../../modules';


// common board, news 
.board-list__wrap {

  .board-list {
    display: flex;
    column-gap: 6rem;
    row-gap: 9.6rem;
    flex-wrap: wrap;
    justify-content: flex-start;

    span{
      display: inline;
    }

    .icon-board-state-new{
      display: inline-block;
      vertical-align: middle;
      margin-left: 0.2em;
    }
  }
}

// common btn style
.btn-promote-board {
  .wrap-img {
    border-radius: 1rem;
    overflow: hidden;
  }

  .board-title {
    width: 100%;
    margin-top: 2rem;
    margin-bottom: 1.7rem;

    word-break: break-all;
    white-space: normal;

    @include style_font(500, 2rem, 1.5, black);
  }
}

// common feature listed
.promote-boards {
  .board-list__wrap {
    .board-list {
      li {
        width: 30.556%;
        min-width: 44rem;

        transform: translateY(-0);
        transition: transform 0.3s;

        .wrap-img{
          height: 26rem;
        }
      }

      li:hover{
        transform: translateY(-3rem);

        .wrap-img{
          box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1);
          transition: box-shadow 0.3s;
        }
      }
    }
  }

  .pagination {
    margin: 0 auto;
    margin-bottom: 96px;
    margin-top: 76px;
  }
}


.promote-media{
  .media-list{
    display: flex;
    // justify-content: space-between;
    text-align: left;
    column-gap: 6rem;

    li{
      width: 30%;

      transform: translateY(3rem);
      transition: all 0.3s;

      .btn-promote-media {
        position: relative;
        box-shadow: 0px 15px 40px rgba(0, 0, 0, 0.1);
        border-radius: 1rem;
        background-color: $white;

        transition: transform 0.3s;
        overflow: hidden;
        
        .wrap-img {
          height: 23rem;
          overflow: hidden;
        }

        .content{
          padding: 3rem;
          display: flex;
          column-gap: 1.6rem;
          align-items: center;

          .icon{
            display: block;
            width: 4.4rem;
            height: 4.4rem;
          }

          .media-title{
            font-size: 2rem;
            line-height: 1.5;
            font-weight: 400;
            text-align: left;
          }
        }

        &::after {
          position: absolute;
          content: '';
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-image: url('../../../../public/asset/icon/icon_play_white.svg');
          background-position: center;
          background-repeat: no-repeat;
          background-size: 13rem;

          background-color: rgba($azure, $alpha: 0.6);
          opacity: 0;
      }
      }

      .btn-promote-media:hover {
        transform: translateY(-3rem);
        &::after {
          opacity: 1;
          background-size: 10rem;
          transition: all 0.4s;
        }
      }

      .btn-promote-media:active {
        &::after {
            background-color: rgba($azure, $alpha: 0.8);
        }
    }
    }
  }
}

@include mobile-max{
  .promote-boards{
    // news 공통
    .board-list__wrap {

      .board-list {
        padding: 0 2rem;
        row-gap: 4rem;

        li {
          width: 100%;
          min-width: 32rem;

          .wrap-img{
            height: 19rem;
          }

          .board-title {
            margin-top: 1rem;
            margin-bottom: 6px;
            font-size: 1.4rem;
          }

          .date {
            display: block;
            font-size: 1.2rem;
          }
        }
      }

      .pagination {
        margin-top: 4rem;
        margin-bottom: 4rem;
      }
    }
  }

  .promote-media{
    .media-list{
      li {
      .btn-promote-media {
          min-width: 25.2rem;
          width: 100%;

          .wrap-img {
              height: 13rem;
          }

          .content {
              height: 7.5rem;
              padding: 1.6rem 2rem 2.4rem 2rem;

              .media-title {
                  font-size: 1.4rem;
              }

              .icon-play {
                  min-width: 2.8rem;
                  height: 2.8rem;
                  top: 2.3rem;
              }
          }
      }
    }
    }
  }
}