@import '../../modules';


// Tmaxos새로운 소식
.main-board {
  .board__container {
    text-align: center;
    padding-top: 9.5rem;
  }

  .board-list__wrap {
    margin-top: 7.6rem;

    .board-list {
      justify-content: space-between;
      column-gap: 1rem;

      .btn-promote-board {
        position: relative;
        width: 335px;
        min-width: 21.6rem;
        text-align: left;

        .wrap-img {
          width: 100%;
          height: 195px;
          border-radius: 1rem;

          background-color: dimgray;
        }

        .date {
          display: none;
        }

        .icon{
          display: none;
        }
      }
    }
  }

  .btn-more {
    margin: 10rem 0;
  }
}

// 뉴스룸
.main-news {
  padding-top: 9.6rem;
  background: $bg_gray_f;
  text-align: center;

  .news-grid {
    margin-top: 7.6rem;
    position: relative;

    display: grid;
    grid-template-columns: 830px 577px;
    justify-content: center;

    column-gap: 3.2rem;
    row-gap: 3rem;

    // 공통
    .btn-top-news,
    .btn-news-another {
      display: flex;
      justify-content: space-between;

      text-align: left;
      border-radius: 1rem;
      background-color: $white;
      overflow: hidden;

      .content {

        .title {
          @include style_font(500, 2.4rem, 1.5, black);
          // 두 줄부터 ... 해야해용
          // white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }

        .date {
          @include style_font(300, 1.8rem, 1.7, $font_color-B0B0B0_grey);
          margin-top: 14px;
        }
      }
      
      .wrap-img{
        overflow: hidden;

        img{
          object-fit: cover;
        }
      }
    }

    .btn-top-news {
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      height: 100%;

      grid-row-start: 1;
      grid-row-end: 4;

      .wrap-img {
        width: 100%;
        height: 439px;
        border-radius: 10px;
      }

      .content {
        position: relative;
        padding: 3.1rem 4rem 4rem 4rem;

        width: 100%;
        // height: 100%;

        .title {
          @include style_font(700, 2.6rem, 1.5, black);
          height: 55px;
          margin-bottom: 8px;
        }

        p {
          line-height: 170%;
          width: 100%;
          letter-spacing: -0.5px;
          color: #666666;
        }

        .date {
          color: #b0b0b0;
        }
      }
    }

    .btn-news-another {
      display: flex;

      .content {
        padding: 4rem 3rem 4rem 4rem;

        .title {
          height: 77px;

        }
      }

      .wrap-img {
        display: inline-block;
        width: 20.8rem;
        height: 100%;
      }
    }
  }

  .btn-more {
    margin: 10rem 0;
  }
}


@include mobile-max {

  .main-board,
  .main-news{
    h3{
      font-size: 1.6rem;
    }
  }


  // 새로운 소식 
  .main-board {
    .board__container {
      padding-top: 4rem;
    }

    .board-list__wrap {
      margin-top: 4rem;

      .board-list {
        flex-wrap: nowrap;
        
        padding: 0 2rem;

        overflow-x: scroll;
        overflow-y: hidden;
        white-space: nowrap;

        gap: 1.2rem;

        .btn-promote-board {
          width: 21.6rem;


          .wrap-img {
            height: 12.6rem;
          }

          .board-title {
            font-size: 1.4rem;
            margin-top: 1rem;
            margin-bottom: 0;
          }
        }
      }
    }

    .btn-more {
      margin: 3.4rem 0 4rem 0;
    }
  }

  // 뉴스룸
  .main-news {
    padding: 4rem 2rem 0 2rem;

    .news-grid {
      margin-top: 3.4rem;

      grid-template-columns: 100%;
      row-gap: 1.2rem;

      // news 공통
      .btn-top-news,
      .btn-news-another {
        .content {
          position: relative;
          width: 100%;

          .title {
            @include style_font(500, 1.4rem, 1.5, black);
            height: auto;
          }

          .date {
            @include style_font(400, 1.2rem, 1.5, $font_color-B0B0B0_grey);
          }
        }
      }

      .btn-top-news {
        .content {
          padding: 2rem;
          height: max-content;

          .date {
            margin-top: 10px;
          }
        }

        .wrap-img {
          min-height: 17rem;
          height: auto;
          border-radius: 1rem 1rem 0 0;
        }
      }

      .btn-news-another {

        .content {
          padding: 2rem;

          .title {
            overflow: hidden;
            white-space: nowrap;

            text-overflow: ellipsis;
          }

          .date {
            margin-top: 8px;
          }
        }

        .wrap-img {
          display: none;
        }
      }
    }

    .btn-more {
      margin: 3.4rem 0 4rem 0;
    }
  }

}