@import '../../modules';

.main-customer {

  .customer__container {
    position: relative;
    // height: 35.7rem;
    height: 29.9rem;
    // display: flex;
    margin-top: 58px;

    background: linear-gradient(90.72deg, #4661FF 0.62%, #4BA5FF 99.38%);
    border-radius: 30px;

    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    // align-items: center;

    // padding-right: 16rem;
    // padding-top: 10.9rem;
    // padding-bottom: 6.9rem;
    // padding: 7rem 16rem;

    // 이미지 
    .img-customer-banner {
      position: absolute;
      bottom: 0;
      left: 13.4rem;
      width: 28.9rem;
      height: 35.7rem;
    }

    // 내용 
    .banner-title {
      // padding: 1em 8.6rem;

      h5 {
        @include style_font(700, 3.6rem, 1.5, $white);
        text-align: left;
        padding-bottom: 3.9rem;
      }

      // p{
      //   @include style_font(400, 2.4rem, 1.5, $white);
      // }
    }

    .btn {
      text-align: center;
      width: 280px;
      height: 76px;

      background: $white;
      border-radius: 48px;
      font-size: 24px;
    }
  }
}


.temp-layout {
  padding-bottom: 2rem;
  padding-right: 20.2rem;
  text-align: left;

  .inquiry-content {
    position: relative;
    padding-left: 12rem;
    padding-bottom: 2.2rem;

    .title {
      position: absolute;
      left: 0;

      display: inline-flex;
      justify-content: center;
      align-items: center;

      width: 11.7rem;
      height: 3rem;
      border-radius: 10rem;
      color: $white;

      font-size: 1.4rem;

      background-color: rgba($royalazure, 0.2);
    }

    .contact-number,
    .contact-email {
      font-size: 2.8rem;
      font-weight: 700;

      color: $white;
      padding-left: 1.8rem;
      padding-bottom: 1rem;
    }

    p {
      padding-left: 1.8rem;
      color: $white;
      font-size: 1.4rem;
    }
  }
}

@include mobile-max {
  .main-customer {
    .customer__container {
      flex-direction: column;
      justify-content: space-between;


      height: 22.3rem;

      margin-top: 0;
      padding: 0;
      padding-top: 2.6rem;
      padding-bottom: 3rem;
      padding-right: 3.3rem;
      padding-left: 4rem;

      border-radius: 0;

      .img-customer-banner {
        width: 10.7rem;
        height: 13.2rem;
        left: 4rem;
      }

      .banner-title {
        align-self: flex-start;
        padding: 0;

        h5 {
          font-size: 1.6rem;
          padding-bottom: 8px;
        }

        p {
          font-size: 1.4rem;
        }
      }


      .btn {
        font-size: 1.4rem;
        width: 14.2rem;
        height: 4rem;
        padding: 0;

        .icon {
          margin-left: 5px;
          width: 1.4rem;
          height: 1.6rem;
        }
      }
    }

  }
}