@import '../../modules';

.dropbox{
  position: relative;
  width: 100%;

  input{
    position: absolute;
    left: 2.1rem;
    width: 20rem;
    font-weight: 500;

    background-color: transparent;
    opacity: 0;
  }

  .value{
    text-align: right;
    
    .icon{
      vertical-align: middle;
      width: 1.3rem;
      height: 100%;
      vertical-align: -1.4em;
    }
  }

  .value.focused{
    outline: 3px solid $azure;
  }

  &.typed.selected button{
    outline: 1px solid #D8D8D8;
  }

  label{
    @include style_font(500, 1.6rem, 1, #91989D);
    transition: all 0.3s;
    position: absolute;
    top: 2.1rem;
    left: 2.1rem;
    user-select: none;
  }
}

.dropdown{
  position: absolute;
  z-index: 9;
  
  ol{
    margin-top: 1rem;
    padding: 6px 0.6rem;
    // !temp
    width: 33rem;
    
    display: flex;
    flex-direction: column;
    gap: 4px;
    
    border-radius: 1rem;
    background-color: $white;
    border: 1px solid #D8D8D8;
    box-shadow: 0px 8px 20px rgba(15, 23, 42, 0.06);
    
    li button{
      text-align: left;
      padding: 1rem 1rem;
      width: 100%;
      @include style_font(400, 1.6rem, 1.4em, #666666);
      &:hover{
        color: $azure;
      }
    }
  }
}


@include mobile-max{
  .dropbox{

    .value{
      height: 4.6rem;
      padding: 1.6rem 1.2rem;

      border-radius: 5px;

      .icon{
        vertical-align: initial;
        width: 1rem;
        height: 1rem;
      }
    }

    label{
      top: 1.6rem;
      font-size: 1.4rem;
    }

    input{
      top: 1.6rem;
    }
  }
}