@import '../../modules';


.board__wrapper.notice{

  .title{
    display: grid;
    grid-template-columns: 10% auto 12%;
    column-gap: 8px;
  }

  .board-list{
    
    li{
      display: grid;
      grid-template-columns: 10% auto 12%;
      align-items: center;
      column-gap: 8px;
      height: 9.5rem;
      border-bottom: 1px solid #ececec;
      
      .--col1{
        justify-self: center;
      }

      .date{
        justify-self: center;
      }
    }
  }
}

@include mobile-max{
  .board__wrapper.notice{

    .title{
      display: none;
    }

    .board-list{
      padding: 0;

      li{
        padding: 0 2rem;
        grid-template-columns: none;
        grid-template-areas:
          "cate date"
          "cont cont";
        
        height: 8.7rem;

        .--col1{
          width: 100%;
          text-align: left;
          grid-area: cate;
          padding-left: 0;


        }

        .cont{
          width: 100%;
          font-size: 1.4rem;
          grid-area: cont;
          padding: 0%;
          margin-left: 2px;
          
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;

          .icon-table_new{
            width: 1.4rem;
            height: 1.4rem;
          }
        }

        .--col3{
          text-align: right;
        }
        .date{
          grid-area: date;
          font-size: 1.2rem;
        }
      }
    }
  }


}