@import '../../modules';

// common rule 
.board__wrapper{
  background-color: $white;

  .cate{
    @include style_font(400, 1.4rem, 1, black);
    padding: 1.1rem 2.2rem;
    background-color: #eaeaea;
    border-radius: 3rem;
    cursor: default;

    &.--pr{
      background-color: #DCE1FF;
    }

    &.--gr{
      background-color: #CFE9FD;
    }
    &.--grsd{
      background-color: #CCF7F7;
    }
    &.--srds{
      background-color: #D3FFDF;
    }
    &.--etc{
      background-color: #EAEAEA;
    }
  }

  .board-list{

    li{
      text-align: center;
      
      .cont{ 
        @include style_font(400, 2rem, 1, #212121);
        // display: inline-block;
        position: relative;
        padding: 3rem 0;
        margin-left: 10rem;
        text-align: left;

        .icon-table_new{
          margin-left: 6px;
        }

        // 게시물이 공지사항 일 때, 
        &.notice::before{
          position: absolute;
          content: '';
          top: 1.2em;
          left: -5rem;
          width: 2.8rem;
          height: 2.8rem;
          vertical-align: middle;
          // background-image: url('../../../public/asset/icon/icon_table_notice.svg');
        }
      }

      .date{
        @include style_font(400, 1.8rem, 1, #797979);
      }
    }
  }
}


@include mobile-max{
  .board__wrapper{
    .cate{
      display: inline-block;
      padding: 6px 10px;
      font-size: 1rem;
    }
  }
}