@import '../../modules';

.osp8112{
  
  .banner{
    position: relative;
    height: calc(42.5rem + 8.4rem);
    background-color: $bg_gray_f;
    padding-top: 8.4rem;

    // 뒷 배경 (원) 설정
    z-index: 0;
    // overflow: hidden;
    background-image: url($img_path_to_pages + 'res/bg_circle_1.svg');
    background-repeat: no-repeat;
    background-size: 1254px;
    background-position: 1212px -636px;

    // &::after{
    //   position: absolute;
    //   content: '';
    //   height: 100%;
    //   z-index: -1;
    //   background-repeat: no-repeat;

    //   background-size: 1254px;
    //   width: 1254px;
    //   height: 1254px;
    //   top: -636px;
    //   right: -536px;
    // }

    .--top{
      padding-top: 11.1rem;
      h2{
        @include style_font(700, 3.8rem, 1, $black_0);
        padding-bottom: 3.7rem;
      }
      p{
        @include style_font(400, 2.4rem, 1, $black_0);
      }
    }

    .--bottom{
      padding-top: 12rem;
      display: flex;
      justify-content: space-between;

      .toggle{
        li{
          display: inline-block;

          button{
            position: relative;
            margin-right: 3.3rem;
            @include style_font(400, 2.4rem, 1, #B0B0B0);
            transition: all 0.3s;

            &::after{
              position: absolute;
              content: '';
              width: 100%;
              height: 2px;
              bottom: -5px;
              left: 0;
              background-color: $azure;
              transform: scaleY(0%);
              transition: transform 0.1s;
            }
          }
          
          .focused{
            color: $azure;
            font-weight: 700;
            &::after{
              transform: scaleY(100%);
            }
          }
        }
      }

      .dropbox{
        width: 33rem;

        label{
          color: $font_color-212121_grey;
        }
      }
    }
  }

  .content{
    // height: 55.5rem;
    padding-top: 5.2rem;
    padding-bottom: 14rem;
    p{
      @include style_font(400, 2rem, 2em, black);
      padding-bottom: 4rem;
    }
  }
}

@include mobile-max{
  .osp8112{

    .banner{
      height: calc(4.8rem + 28.5rem);
      padding: 4.8rem 3rem 3rem 3rem;

      background-size: 554px;
      background-position-x: calc(100% + 250px);
      background-position-y: bottom;

      .--top{
        padding-top: 6rem;

        h2{
          font-size: 2.2rem;
          padding-bottom: 2rem;

        }

        p{
          font-size: 1.4rem;
        }
      }

      .--bottom{
        padding-top: 4.7rem;

        flex-direction: column;

        row-gap: 3rem;

        .toggle{

          li{

            button{
              font-size: 1.6rem;

            }
          }
        }
      }
    }

    .content{
      padding-top: 2.9rem;
      padding-bottom: 5rem;
      padding: 2.9rem 2rem 5rem 2rem;

      p{
        width: 100%;
        font-size: 1.2rem;
      }
    }
  }
}