@import '../../modules';

.view{
  position: relative;

  text-align: left;
  margin-bottom: 5.6rem;



  // 위쪽 삼각형
  z-index: 0;
  &::before{
    z-index: -1;
    position: absolute;
    content: '';
    top: 8rem;
    left: -8rem;
    width: calc(100% + 16rem);
    height: 50rem;
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: 100% auto;
    background-image: url($img_path_to_pages + 'res/bg-6115-t.svg');
  }
  &::after{
    z-index: -1;
    position: absolute;
    content: '';
    top: 58rem;
    left: -8rem;
    width: calc(100% + 16rem);
    height: calc(100% - 58rem + 16.4rem);
    background-color: #F1F2F2;
  }

  .cont{
    background-color: white;
    padding: 58px 70px 70px;
  }

  .title{
    font-size: 28px;
    font-weight: bold;
    text-align: center;

    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    &::before{
      background-image: url($img_path_to_pages + 'ico_quotes.svg');

      content:'';
      background-repeat: no-repeat;
      display: block;
      width: 30px;
      height: 30px;
      margin-bottom: 38px;
    }
  }

  .date{
    color: #797979;
    display: block;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 48px;
  }

  hr.line{
    height: 2px; 
  }

  .content{
    padding: 48px 0;
    font-size: 15px;
    line-height: 30px;
    color: #3C3C3C;
  }





}

.wrap-content.promotion-wordView{
  text-align: center;  
  padding: 96px 0 20.4rem 0;
  overflow: hidden;
  
  background-color: #F8F9FA;
  
  .view{
    
    .wrap-img{
      margin-top: 4.8rem;
      margin-bottom: 48px;

      width: 1300px;
      height: 768px;
      img{
        height: 100%;
      }
    }


  }
  .btn__wrapper{
    position: absolute;
    width: 100%;
    text-align: center;
    margin-top: 2.4rem;

  }
  
  a.btn-link-list{
    margin-top: 0;
    background-color: white;
  }

}


// * 👽️responsive
@include mobile-max{
  
  .view{
    padding: 0 0;
    margin-bottom: 3.4rem;

    &::after{
      width: calc(100% + 2rem);
      left: -1rem;
      top: 26.6rem;
      height: calc(100% - 26.6rem + 16.4rem);
    }

    &::before{
      width: calc(100% + 2rem);
      left: -1rem;
      top: 0.6rem;
      // background-image: url($img_path_to_pages + 'res/bg-6115-t_m.svg');
      height: 26rem;

    }


    .cont{
      padding: 2.4rem 2rem;

      .title{
        font-size: 1.6rem;
        line-height: 1.4em;

        &::before{
          margin-bottom: 1.4rem;
        }
      }

      .date{
        font-size: 1.2rem;
        color: #B0B0B0;
        margin-bottom: 2.4rem;
      }

      hr.line{
        height: 1px;
      }


      .content{
        padding: 0 0 2.4rem 0;
        font-size: 1.4rem;
        line-height: 1.6em;
        color: #666666;
      }
    }
  } 
  .wrap-content.promotion-wordView{
    padding: 4rem 2rem 10.6rem 2rem;
    text-align: center;

    .view .cont {

      .wrap-img{
        margin-top: 2.4rem;
        margin-bottom: 2rem;
        width: 100%;
        height: 16rem;
      }
    }
    .btn__wrapper{
      width: calc(100% - 4rem);
    }
  }
}